body {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  color: #fff;
  background-image: url("../images/bg.webp");
  background-color: #202020;
  cursor: default;
}

.main {
  flex-grow: 1;
}

a {
  &:active,
  &:hover,
  &:focus {
    outline: 0;
    outline-offset: 0;
    text-decoration: none;
    color: inherit;
  }
}

.container {
  margin: 0 auto;
  padding: 0 50px;
  width: 100%;
  max-width: 1590px;

  &::before,
  &::after {
    display: none;
  }

  @include r(1600) {
    max-width: 1300px;
  }

  @include r($lg) {
    padding: 0 30px;
    max-width: 834px;
  }

  @include r($md) {
    padding: 0 17px;
  }
}

section {
  padding: 0 0 300px;

  @include r(1600) {
    padding: 0 0 250px;
  }

  @include r($xl) {
    padding: 0 0 150px;
  }

  @include r($lg) {
    padding: 0 0 100px;
  }
}

h3 {
  margin-bottom: 100px;
  font-weight: 500;
  font-size: 48px;
  line-height: 72px;

  @include r(1600) {
    margin-bottom: 75px;
    line-height: 1;
  }

  @include r($xl) {
    margin-bottom: 50px;
    font-size: 36px;
    line-height: 42px;
  }

  @include r($lg) {
    margin-bottom: 35px;
    font-size: 24px;
    line-height: 36px;
  }

  @include r($md) {
    margin-bottom: 20px;
    text-align: center;
  }
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 63px;
  height: 56px;
  outline: 0;
  outline-offset: 0;
  border: none;
  border-radius: 90px;
  box-shadow: none;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  text-decoration: none;
  background-image: linear-gradient(90.71deg, #8d5bdf 0%, #1860ed 100%);
  transition: box-shadow 0.2s, color 0.2s;

  &:hover,
  &:active,
  &:disabled,
  &:visited,
  &:focus {
    outline: 0;
    outline-offset: 0;
    text-decoration: none;
    color: inherit;
    background-image: linear-gradient(90.71deg, #8d5bdf 0%, #1860ed 100%);
  }

  &:hover {
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.25);
  }

  &:active {
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.35), inset 0 4px 15px rgba(255, 255, 255, 0.39);
  }

  &:disabled {
    box-shadow: inset 0 0 50px rgba(0, 0, 0, 0.25);
    color: #dfdfdf;
  }

  @include r(1600) {
    padding: 0 55px;
    height: 50px;
    font-size: 18px;
    line-height: 26px;
  }

  @include r($xl) {
    padding: 0 45px;
    font-size: 16px;
    line-height: 24px;
  }

  @include r($lg) {
    padding: 0 63px;
  }
}

.btn-content {
  margin: 35px 0 0;
  width: fit-content;

  @include r($md) {
    margin: 25px auto 0;
    padding: 0;
    width: 200px;
  }
}

.popup {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  opacity: 0;
  background-color: rgba(#000, 0.8);
  transition: all 0.2s;

  &__wrapper {
    position: relative;
    padding: 107px 50px 68px;
    width: 100%;
    max-width: 800px;
    border-radius: 20px;
    background-color: #fff;

    @include r($xl) {
      padding: 90px 30px 55px;
    }

    @include r($lg) {
      padding: 84px 18px 50px;
      max-width: 774px;
    }
  }

  h2 {
    margin: 0 auto 25px;
    width: fit-content;
    max-width: 600px;
    font-weight: 500;
    font-size: 48px;
    line-height: 72px;
    text-align: center;
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    background-image: linear-gradient(90.71deg, #8d5bdf 0%, #1860ed 100%);
    background-clip: text;

    @include r(1600) {
      font-size: 44px;
      line-height: 58px;
    }

    @include r($xl) {
      font-size: 40px;
      line-height: 48px;
    }

    @include r($lg) {
      font-size: 32px;
      line-height: 48px;
    }
  }

  &__close {
    position: absolute;
    right: 66px;
    top: 54px;
    cursor: pointer;

    @include r($lg) {
      right: 52px;
      top: 45px;
    }

    @include r($md) {
      right: 30px;
      top: 40px;
    }

    @include r($sm) {
      right: 16px;
      top: 35px;
    }
  }

  .btn {
    margin: 0 auto;
    width: fit-content;
  }

  &--active {
    visibility: visible;
    opacity: 1;
  }

  @include r($lg) {
    padding: 0 30px;
  }

  @include r($md) {
    padding: 0 17px;
  }
}

.cookie {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 130px;
  z-index: 20;
  margin: 0 auto;
  padding: 0 50px;
  width: 100%;
  max-width: 1590px;
  transition: all 0.2s;
  animation-name: cook;
  animation-duration: 1.5s;

  &__box {
    position: relative;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    padding: 84px 50px 50px;
    border-radius: 20px;
    background-color: #fff;

    @include r(1600) {
      padding: 60px 40px 40px;
    }

    @include r($xl) {
      padding: 55px 35px 35px;
    }

    @include r($lg) {
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 69px 8px 35px;
    }
  }

  h4 {
    margin: 0 0 15px;
    width: fit-content;
    font-weight: 600;
    font-size: 24px;
    line-height: 1.2;
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    background-image: linear-gradient(90.71deg, #8d5bdf 0%, #1860ed 100%);
    background-clip: text;

    @include r(1600) {
      margin-bottom: 10px;
      font-size: 22px;
    }

    @include r($xl) {
      font-size: 20px;
    }

    @include r($lg) {
      margin: 0 auto 25px;
      text-align: center;
    }
  }

  p {
    font-weight: 400;
    font-size: 20px;
    line-height: 1.2;
    color: #454545;

    a {
      -webkit-text-fill-color: transparent;
      text-fill-color: transparent;
      background-image: linear-gradient(90.71deg, #8d5bdf 0%, #1860ed 100%);
      background-clip: text;
    }

    @include r(1600) {
      font-size: 18px;
    }

    @include r($xl) {
      font-size: 16px;
    }

    @include r($lg) {
      margin-bottom: 25px;
      font-size: 14px;
      text-align: center;
    }
  }

  .btn {
    margin-left: 80px;

    @include r($xl) {
      margin-left: 60px;
    }

    @include r($lg) {
      margin: 0 auto;
    }
  }

  &__close {
    position: absolute;
    right: 56px;
    top: 56px;
    cursor: pointer;

    @include r(1600) {
      top: 40px;
    }

    @include r($lg) {
      right: 21px;
      top: 26px;
    }
  }

  &--inactive {
    visibility: hidden;
    opacity: 0;
  }

  @include r(1600) {
    max-width: 1300px;
  }

  @include r($lg) {
    bottom: 215px;
    padding: 0 30px;
    max-width: 834px;
  }

  @include r($md) {
    bottom: 167px;
    padding: 0 17px;
  }
}

.form-accept {
  padding-left: 15px;
  font-weight: 500;
  font-size: 24px;
  line-height: 36px;
  color: #fff;

  @include r($lg) {
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
  }

  @include r($md) {
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
  }
}

.figure {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: -3;
  background-color: transparent;

  img {
    max-width: 800px;
  }

  @include r(1600) {
    img {
      max-width: 700px;
    }
  }

  @include r($xl) {
    img {
      max-width: 600px;
    }
  }

  @include r($md) {
    img {
      max-width: 400px;
    }
  }
}

.figure-top__wrapper {
  position: absolute;
  right: 0;
  top: 0;
  z-index: -3;
  width: 1200px;
  height: 1200px;
  overflow: hidden;
  background-color: transparent;
}

.figure-top__inner {
  width: 100%;
  height: 100%;
  filter: blur(50px);
}

.figure-top__circle,
.figure-top__triangle {
  position: absolute;
  object-fit: contain;
  animation: orbit 10s infinite linear;
}

.figure-top__circle {
  right: 10%;
  top: 10%;
  width: 600px;
  height: 600px;
  animation: orbit 10s infinite linear reverse;

  @include r(1600) {
    top: 14%;
    width: 500px;
    height: 500px;
  }

  @include r($xl) {
    width: 450px;
    height: 450px;
  }

  @include r($lg) {
    top: 8%;
    width: 400px;
    height: 400px;
  }

  @include r($md) {
    right: 6%;
    top: 6%;
    width: 300px;
    height: 300px;
  }

  @include r($sm) {
    right: 2%;
    top: 2%;
    width: 200px;
    height: 200px;
  }
}

.figure-top__triangle {
  right: 0;
  top: 8%;
  width: 500px;
  height: 465px;

  @include r(1600) {
    width: 400px;
    height: 372px;
  }

  @include r($lg) {
    top: 6%;
  }

  @include r($md) {
    top: 0;
    width: 300px;
    height: 279px;
  }

  @include r($md) {
    top: 0;
    width: 200px;
    height: 186px;
  }
}

@keyframes orbit {
  from {
    transform: rotate(0deg) translateX(40px) rotate(0deg);
  }

  to {
    transform: rotate(360deg) translateX(40px) rotate(-360deg);
  }
}

@keyframes printed_text {
  from {
    visibility: hidden;
    width: 0;
  }

  to {
    visibility: visible;
    width: 100%;
  }
}

@keyframes appear {
  from {
    visibility: hidden;
    opacity: 0;
    color: transparent;
  }

  to {
    visibility: visible;
    opacity: 1;
    color: #fff;
  }
}

@keyframes cook {
  from {
    transform: translateY(20px);
  }

  to {
    transform: translateY(0);
  }
}